import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    testStore: 'vuex 测试'
  },
  getters: {
    testStore: state => {
      return state.testStore
    }
  },
  mutations: {
    setTestStore(state, tabInfo) {
      state.testStore = testStore
    }
  }
})



