<template>
  <div class="flask-player">
    <div class="title">FLASK URL TEST</div>
    <div class="img-div">
      <img :src="liveSrc" alt="">
    </div>
    <el-input class="url-input" v-model="liveSrc" placeholder="请输入Flask Url"></el-input>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: "FlaskPlayer",
    components: {},
    props: {},
    data() {
      return {
        liveSrc: ''
      }
    },
    methods: {},
    mounted() {

    },
    watch: {
      liveSrc(val) {}
    },
    computed: {
      ...mapGetters([]),
      getComponentName() {
        return "FlaskPlayer"
      }
    }

  }
</script>

<style lang="scss" scoped>
  .flask-player {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FAFAFA;

    .title{
      font-size: 2rem;
      margin: 2.5%;
    }
    .img-div {
      width: 50%;
      height: 70%;
      background-color: #000000;
      margin-bottom: 1.5%;
      display: flex;
      justify-content: center;
      align-items: center;
      img{
        max-height: 100%;
        max-width: 100%;
      }
    }
    .url-input{
      width: 50.1%;
    }
  }
</style>



