import Vue from 'vue'
import Router from 'vue-router'
import FlaskPlayer from "@/components/commom/FlaskPlayer"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'FlaskPlayer',
      component: FlaskPlayer
    },
    {
      path: '/FlaskPlayer',
      name: 'FlaskPlayer',
      component: FlaskPlayer
    }
  ]
})



